// 
// light-mode.scss
// 


// Dark Variable
html[data-bs-theme="dark"] {

    // <=============================================>
    // Bootstrap variable
    // <=============================================>

    // scss-docs-start box-shadow-variables
    --#{$prefix}box-shadow:                                    0px 0px 35px 0px #{rgba(66, 72, 80, 0.15)};
    --#{$prefix}box-shadow-sm:                                 0 .75rem 6rem #{rgba(56, 65, 74,.03)};
    --#{$prefix}box-shadow-lg:                                 0 0 45px 0 #{rgba($black, 0.12)};
    --#{$prefix}box-shadow-inset:                              inset 0 1px 2px #{rgba($black, .075)};
    
    
    --#{$prefix}light:                                   #3a4250;
    --#{$prefix}light-rgb:                                 #{to-rgb(#3a4250)};

    --#{$prefix}dark:                                    #f7f7f7;
    --#{$prefix}dark-rgb:                                 #{to-rgb(#f7f7f7)};


    // <=============================================>
    // Custome variable
    // <=============================================>

    // Left Sidebar
    --#{$prefix}bg-leftbar:                                      #{$white};
    --#{$prefix}bg-leftbar-dark:                                 #313844;
    --#{$prefix}bg-leftbar-brand:                                #4a81d4;
    --#{$prefix}bg-leftbar-gradient:                             #5b69bc;

    // user box
    --#{$prefix}left-menu-user-box-border:                       #f5f5f5;
    --#{$prefix}left-menu-dark-user-box-border:                  #{rgba($white, .2)};

    // Dark leftbar menu color
    --#{$prefix}menu-item-color-dark:                            #9097a7;
    --#{$prefix}menu-item-hover-color-dark:                      #c8cddc;
    --#{$prefix}menu-item-active-color-dark:                     #{$white};

    //Menu item
    --#{$prefix}menu-item:                                       #6e768e;
    --#{$prefix}menu-item-hover:                                 #71b6f9;
    --#{$prefix}menu-item-active:                                #71b6f9;
    --#{$prefix}menu-sub-item-active:                            #71b6f9;

    // Horizontal Menu Item
    --#{$prefix}hori-menu-item-color:                            #919eab;

    //Rightbar Width
    --#{$prefix}rightbar-bg:                                     #323a46;

    //Topbar Background
    --#{$prefix}bg-topbar-light:                                 #353d4a;
    --#{$prefix}bg-topbar-dark:                                  #2c333e;

    // Leftbar item shadow (Condensed item)
    --#{$prefix}box-shadow-condensed:                            3px 5px 10px 0 rgba(50,58,66); 

    // Page title 
    --#{$prefix}page-title-color:                               #f8f9fa;

    // Topbar Search
    --#{$prefix}topbar-search-bg:                               #323a46;
    --#{$prefix}topbar-search-bg-dark:                          #323a46;

    // Footer
    --#{$prefix}footer-bg:                                      #2c333e;
    --#{$prefix}footer-color:                                   #adb5bd;
    --#{$prefix}footer-link-hover-color:                        #a2abb4;

    // Logo box 
    --#{$prefix}logo-box-background:                           #{$white};

    // User authentication Background
    --#{$prefix}auth-bg:                                        #3e4852;
    --#{$prefix}auth-bg-alt:                                    #3e4852;

    // Chat widget
    --#{$prefix}chat-primary-user-bg:                           #404c58;
    --#{$prefix}chat-secondary-user-bg:                         #404c58;

    // Card overlay 
    --#{$prefix}card-overlay-bg:                                #{rgba($white, 0.8)};

    // Input light gray
    --#{$prefix}input-light-bg:                                 #3b4550;

    // Email
    --#{$prefix}email-msg-container-bg:                         #333a47;
}