// 
// tables.scss
//

th {
    font-weight: $table-head-font-weight;
}

//Table centered (Custom)
.table-centered {

    td,
    th {
        vertical-align: middle !important;
    }
}


// Table 

.table-nowrap {

    th,
    td {
        white-space: nowrap;
    }
}

// Custom table components (Custom)
.table {
    .table-user {
        img {
            height: 30px;
            width: 30px;
        }
    }
}

.action-icon {
    color: var(--#{$prefix}light-text-emphasis);
    font-size: 1.2rem;
    display: inline-block;
    padding: 0 3px;

    &:hover {
        color: var(--#{$prefix}body-color);
    }
}



table {

    // Table Hover
    &.table-hover {

        thead,
        tbody {
            tr {
                &:hover {
                    >* {
                        --ct-table-accent-bg: #f1f3fa;
                    }
                }
            }
        }
    }

    // Table Active 
    tr {
        &.table-active {
            --ct-table-accent-bg: rgba(222, 226, 230, 0.3);
        }
    }

    //  Table Striped
    &.table-striped {
        tbody {
            tr {
                &:nth-of-type(odd) {
                    >* {
                        --ct-table-accent-bg: rgba(222, 226, 230, 0.3);
                    }
                }
            }
        }
    }
}