//
// right-sidebar.scss
//

.right-bar {
    width: $rightbar-width !important;


    .rightbar-title {
        background-color: $primary;
        padding: 25.5px 25px;
        color: $white;
    }

    .right-bar-toggle {
        background-color: var(--#{$prefix}dark-text-emphasis);
        height: 24px;
        width: 24px;
        line-height: 24px;
        color: var(--#{$prefix}light-border-subtle);
        text-align: center;
        border-radius: 50%;
        margin-top: -4px;

        &:hover {
            background-color: var(--#{$prefix}light);
        }
    }

    .user-box {
        padding: 25px;
        text-align: center;

        .user-img {
            position: relative;
            height: 64px;
            width: 64px;
            margin: 0 auto 15px auto;

            .user-edit {
                position: absolute;
                right: -5px;
                bottom: 0px;
                height: 24px;
                width: 24px;
                background-color: $white;
                line-height: 24px;
                border-radius: 50%;
                box-shadow: $components-shadow-lg;
            }
        }

        h5 {
            margin-bottom: 2px;

            a {
                color: var(--#{$prefix}dark);
            }
        }
    }
}