// 
// light-mode.scss
// 


:root {
    
    // <=============================================>
    // Custome variable
    // <=============================================>

    // Left Sidebar
    --#{$prefix}bg-leftbar:                                      #{$white};
    --#{$prefix}bg-leftbar-dark:                                 var(--#{$prefix}dark-text-emphasis);
    --#{$prefix}bg-leftbar-brand:                                #4a81d4;
    --#{$prefix}bg-leftbar-gradient:                             #{$purple};

    // user box
    --#{$prefix}left-menu-user-box-border:                       #f5f5f5;
    --#{$prefix}left-menu-dark-user-box-border:                  #{rgba($white, .2)};

    // Dark leftbar menu color
    --#{$prefix}menu-item-color-dark:                            #9097a7;
    --#{$prefix}menu-item-hover-color-dark:                      #c8cddc;
    --#{$prefix}menu-item-active-color-dark:                     #{$white};

    //Menu item
    --#{$prefix}menu-item:                                       #6e768e;
    --#{$prefix}menu-item-hover:                                 #{$primary};
    --#{$prefix}menu-item-active:                                #{$primary};
    --#{$prefix}menu-sub-item-active:                            #{$primary};

    // Horizontal Menu Item
    --#{$prefix}hori-menu-item-color:                            #6e7488;

    //Rightbar Width
    --#{$prefix}rightbar-bg:                                     #{$white};

    //Topbar Background
    --#{$prefix}bg-topbar-light:                                 #{$white};
    --#{$prefix}bg-topbar-dark:                                  #38414a;

    // Leftbar item shadow (Condensed item)
    --#{$prefix}box-shadow-condensed:                            3px 5px 10px 0 rgba(154,161,171,.2); 

    // Page title 
    --#{$prefix}page-title-color:                                var(--#{$prefix}light);

    // Topbar Search
    --#{$prefix}topbar-search-bg:                               #e2e6ea;
    --#{$prefix}topbar-search-bg-dark:                          #414b56;

    // Footer
    --#{$prefix}footer-bg:                                      #e3e9ed;
    --#{$prefix}footer-color:                                   var(--#{$prefix}body-color);
    --#{$prefix}footer-link-hover-color:                        #636b72;

    // Logo box 
    --#{$prefix}logo-box-background:                           #{$white};

    // User authentication Background
    --#{$prefix}auth-bg:                                        #{$white};
    --#{$prefix}auth-bg-alt:                                    #6658dd;

    // Chat widget
    --#{$prefix}chat-primary-user-bg:                           #fef5e4;
    --#{$prefix}chat-secondary-user-bg:                         #f1f3fa;

    // Card overlay 
    --#{$prefix}card-overlay-bg:                                #{rgba($white, 0.8)};

    // Input light gray
    --#{$prefix}input-light-bg:                                 #f3f7f9;

    // Email
    --#{$prefix}email-msg-container-bg:                          #{lighten($white, 1%)};
}
