// 
// profile.scss
//

.profile-pills {
    li {
        a {
            color: var(--#{$prefix}dark-border-subtle) !important;
            padding: 0 10px;
            display: block;
            line-height: 30px !important;

            i {
                font-size: 14px;
            }
        }
    }
}

.comment-footer {
    display: block;
    font-size: 12px;

    i {
        font-size: 13px;
        margin-right: 7px;
    }
}

.comment-footer,
.comment-footer a {
    color: var(--#{$prefix}light);

    &:hover {
        color: $primary;
    }
}