//
// morris.scss
//

.morris-chart {
    text {
        font-family: $font-family-secondary !important;
        fill: var(--#{$prefix}dark-border-subtle);
    }
}

.morris-hover {
    position: absolute;
    z-index: 10;

    &.morris-default-style {
        font-size: 12px;
        text-align: center;
        border-radius: 5px;
        padding: 10px 12px;
        background: var(--#{$prefix}dark);
        color: var(--#{$prefix}light);
        font-family: $font-family-base;
        border: none;

        .morris-hover-row-label {
            font-weight: bold;
            margin: 0.25em 0;
            font-family: $font-family-secondary;
        }

        .morris-hover-point {
            white-space: nowrap;
            margin: 0.1em 0;
            color: var(--#{$prefix}dark-bg-subtle) !important;
        }
    }
}