// 
// gallery.scss
//

.portfolioFilter {
    a {
        transition: all 0.3s ease-out;
        color: var(--#{$prefix}dark);
        border-radius: 3px;
        padding: 5px 10px;
        display: inline-block;
        font-weight: $font-weight-medium;

        &:hover {
            color: $primary !important;
        }
    }

    a.current {
        color: $primary !important;
    }
}


.thumb {
    background-color: $card-bg;
    border-radius: 3px;
    box-shadow: $box-shadow-sm;
    margin-top: 24px;
    padding: 10px;
}

.thumb-img {
    border-radius: 2px;
    overflow: hidden;
}

.gal-detail {
    h4 {
        margin-top: 16px;
        font-size: 16px;
    }
}